// depenencies
@use "config";

// set margins for standard content containers - because can't set a default class for squiz matrix CMS content containers
.ds-page-content {
    > { 
        div:not(.ds-section):not(.ds-block),
        .ds-section > div:not(.ds-block),
        section,
        article,
        form {
            padding: 0 20px;
            margin: 0 auto;

            @media (min-width: 640px) {
                padding: 0 40px;
                max-width: 1280px + 80px; // 1280 + 80 (padding)
            }
        }
        div:not(.ds-section),
        section,
        article,
        form {
            // add bottom margin to last child of page if no bg color
            &:last-child {
                margin-bottom: 4rem;
            }
        }

        div:not(.ds-section),
        section,
        article {
            .ds-block {
                padding: 0;
            }
        }
    }
}

div.ds-block {
    clear: both; /* incase there is a float above */
    max-width: 100%;
    padding: 0 20px;
    margin: 0 auto;

    @media (min-width: 640px) {
        padding: 0 40px;
        max-width: 1280px + 80px; // 1280 + 80 (padding)
    }

    .ds-block {
        padding: 0;
    }
}

// .ds-block__wrapper wraps block content
