// block width modifiers: usage .ds-block--[medium|wide|full-width]
div.ds-block {

    &--wide {
        //> .ds-block__wrapper {
            max-width: 1600px;
        //}
    }

    &--full-width {
        padding: 0;
        margin: 0;
        //.ds-block__wrapper {
            max-width: 100%;
            margin: 0;
            padding: 0;
        //}
    }
}

.ds-block__header {
    text-align: left;
    max-width: 1280px;
    margin: 0 auto;

    &--center-align {
        text-align: center;

        > * {
            margin-left: auto;
            margin-right: auto;
        }
    }

    &--right-align {

        text-align: right;
        > * {
            margin-left: auto;
            margin-right: 0;
        }
    }

}
